import { Injectable, TemplateRef } from '@angular/core';

/**
 * @hidden
 */
@Injectable()
export class TooltipTemplateService {

  private seriesTemplates: Array<TemplateRef<any>>;
  private template: TemplateRef<any>;
  private sharedTemplate: TemplateRef<any>;

  public setTemplate(template: TemplateRef<any>): void {
    this.template = template;
  }

  public getTemplate(seriesIndex: number): TemplateRef<any> {
    if (this.seriesTemplates && this.seriesTemplates[seriesIndex]) {
        return this.seriesTemplates[seriesIndex];
    }

    return this.template;
  }

  public setSeriesTemplates(seriesTemplates: Array<TemplateRef<any>>): void {
    this.seriesTemplates = seriesTemplates;
  }

  public setSharedTemplate(sharedTemplate: TemplateRef<any>): void {
    this.sharedTemplate = sharedTemplate;
  }

  public getSharedTemplate(): TemplateRef<any> {
    return this.sharedTemplate;
  }

}



